import React from 'react'
import screw from '../images/img5.avif'
function AboutUs() {
  return (
    <section className="section-about" id='section1'>
    <div className="content-width reveal">
        <header className="about-header">
            <h3><span className="span-about">A Propos</span> De Nous</h3>
        </header>
        <div className="about-content">
            <img src={screw} alt="Screw" className='img-aboutus' loading='lazy'/>
            <div className="text-block">
                <p className="text-header">Qui Sommmes Nous</p>
                <p>ACEM PLUS a été fondée en 2012 par Monsieur BEN SALEM HAMOUDA, qui en est le gérant et le président.<br />
<br />Filiale de la maison mère ACEM, ACEM PLUS est principalement dédiée à la fabrication de matériel pour les collectivités locales et adapté aux besoins environnementaux des territoires tunisiens.<br />
<br />Actuellement, ACEM PLUS occupe une position de premier plan sur le marché local, tant en matière de la commercialisation de ses produits que de sa part significative dans les exportations vers les pays du Maghreb et d'Afrique.<br />
<br />Avec la création de plus de 25 emplois et un atelier couvert de près de 1800 m², ACEM PLUS joue un rôle essentiel dans le secteur.</p>
            </div>
        </div>
    </div>
</section>
  )
}

export default AboutUs