import React, { useState } from 'react';
import Navbar from './Navbar';
import logo from '../images/logo.avif';

function Header() {
    const [isActive, setIsActive] = useState(false);

    const handleNavMenuClick = () => {
        setIsActive(!isActive);
    };
    const handleNavLinkClick = () => {
        setIsActive(false); // Set navbar to inactive when a link is clicked
    };
    return (
        <div className='template-width'>
            <div className='menu-wrapper'>
                <div className={`content-header ${isActive ? 'activee' : ''}`}>
                    <div className='logo'><a href="index.html"><img src={logo} alt="Logo" width="49" height="72" /></a></div>
                    <Navbar onNavMenuClick={handleNavMenuClick} onNavLinkClick={handleNavLinkClick} />
                </div>
            </div>
        </div>
    );
}

export default Header;
