import React, { useState } from 'react';
import { HashLink } from 'react-router-hash-link/dist/react-router-hash-link.cjs.production';
function Navbar({ onNavMenuClick,onNavLinkClick }) {
    const [isNavOpen, setIsNavOpen] = useState(false);

    const toggleNav = () => {
        setIsNavOpen(!isNavOpen);
        onNavMenuClick();
    };
    const handleNavLinkClick = () => {
      setIsNavOpen(false);
      onNavLinkClick();};
  return (
    <nav className={`navbar ${isNavOpen ? 'show' : ''}`}>
      <div className='sublist'>
      <button
          className="menu-items nav-menu"
          onClick={toggleNav}
          aria-label="Toggle Menu">
          &#9776;
      </button>
        <div className="nav-links">
        <HashLink to="/#section0" smooth className='menu-items nav-item' onClick={handleNavLinkClick}>Accueil</HashLink>
        <HashLink to="/#AboutUs" smooth className='menu-items nav-item'  onClick={handleNavLinkClick}>A Propos</HashLink>
        <HashLink to="/#Services" smooth className='menu-items nav-item'  onClick={handleNavLinkClick}>Services</HashLink>
        <HashLink to="/#References" smooth className='menu-items nav-item'  onClick={handleNavLinkClick}>Références</HashLink>
        <HashLink to="/products/#Products" className='menu-items nav-item'  onClick={handleNavLinkClick}>Produits</HashLink>
        <HashLink to="/#ContactUs" smooth className='menu-items nav-item'  onClick={handleNavLinkClick}>Contact</HashLink>

        </div>
      </div>
    </nav>
  );
}

export default Navbar;
